@import url('https://fonts.googleapis.com/css2?family=Caveat:wght@400;500;600&display=swap');

.App {
  background-color: #b0d4b6;
  background-image: radial-gradient(#b0d4b6, #8abf93);
  text-align: center;
  overflow: hidden;
  font-family: 'Caveat', cursive;
  font-size: calc(10px + 8vmin);
  color: #06130f;
}

/*https://fonts.google.com/specimen/Kalam?preview.text=I%20Love%20You%2012:34:56:78&classification=Handwriting*/
/*https://fonts.google.com/specimen/Caveat?preview.text=I%20Love%20You%2012:34:56:78&classification=Handwriting*/
.App-header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.App-footer{
  max-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-header p {
  max-width: min(80vw, 900px);
}

.App-header em {
  font-weight: bold;
  color: #FFF1FA;
}

.Foot {
  position: fixed;
  bottom: 0;
}
